import React, { Component } from 'react';
import lod from '../img/svg/refresh.svg'


class LoginReg extends Component {


    sendLogData() {

        this.setState(() => {
            return {
                loading: true
            }
        })
        let username = document.getElementById('username').value
        let pass = document.getElementById('pass').value

        fetch('/api/login', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({

                "username": username,
                "password": pass,



            })
        }).then(res => res.json())
            .then(parJson => {
                if (parJson.username === username) {




                    window.location.href = "http://0.0.0.0:5000/main";








                }
                else if (parJson.error) {
                    alert(parJson.error)

                    this.setState(() => {
                        return {
                            loading: false
                        }
                    })

                }

            })


            .catch((error) => {
                alert(error)
                this.setState(() => {
                    return {
                        loading: false
                    }
                })
            })



    }

    sendRegData() {

        this.setState(() => {
            return {
                loading: true
            }
        })

        let email = document.getElementById('email').value
        let pass = document.getElementById('pass').value
        let name = document.getElementById('name').value
        let username = document.getElementById('username').value
        let occupation = document.getElementById('occupation').value
        let phone_num = document.getElementById('phone_num').value

        if (email.indexOf('@') === -1 || email.indexOf('.') === -1) {
            alert("Invalid email ")
            return
        }

        if (pass.length < 8) {
            alert("Password should be greater than 8 letters")
            return
        }


        fetch('/api/register', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                "user": {
                    "username": username,
                    "email": email,
                    "password": pass,
                    "phone_num": phone_num,
                    "occupation": occupation,
                    "full_name": name

                }
            })
        })
            .then(res => res.json())
            .then(parJson => {
                if (parJson.user.token) {
                    this.setlog()
                    alert("Registration Complete Now log in ")
                    pass = ""

                    this.setState(() => {
                        return {
                            loading: false
                        }
                    })



                }
                else if (parJson.error) {
                    alert(parJson.error)
                    this.setState(() => {
                        return {
                            loading: false
                        }
                    })

                }

            }).catch((error) => {

                alert(error)
                this.setState(() => {
                    return {
                        loading: false
                    }
                })
            })







    }

    setreg() {

        this.setState(() => {
            return { loc: "reg" }
        })
    }
    setlog() {

        this.setState(() => {
            return { loc: "log" }
        })
    }

    constructor(props) {
        super(props)
        this.setreg = this.setreg.bind(this)
        this.setlog = this.setlog.bind(this)
        this.sendLogData = this.sendLogData.bind(this)
        this.sendRegData = this.sendRegData.bind(this)
        this.state = {
            loc: "log",
            loading: false
        }
    }


    render() {
        return (
            <div >
                <div className="overlay" onClick={this.props.remLogReg}></div>
                <div className="gotohomebtn" onClick={this.props.gotohome} >B</div>

                <div className="logReg">

                    <div className="logNav">

                        <span onClick={this.setlog} className={this.state.loc === "log" ? "active log" : "log"}  >Login</span>
                        <span onClick={this.setreg} className={this.state.loc === "reg" ? "active reg " : "reg"} >Register</span>

                    </div>

                    {this.state.loc === "log" ?
                        null
                        :
                        <input id="name" type="text" placeholder="Name" />


                    }
                    {this.state.loc === "log" ?
                        null
                        :
                        <input id="email" type="text" placeholder="Email" />


                    }

                    {this.state.loc === "log" ?
                        null
                        :

                        <input id="phone_num" type="text" placeholder="Phone Number" />

                    }
                    {this.state.loc === "log" ?
                        null
                        :
                        <input id="occupation" type="text" placeholder="Occupation" />


                    }




                    <input id="username" type="text" placeholder="Username" />


                    <input id="pass" type="password" placeholder="Password" />



                    {this.state.loc === "log" ?
                        <button className="loginBtn btnbtn" onClick={this.sendLogData}>
                            {this.state.loading ? <img className="loadingsvg" src={lod} /> : "Login"}</button>
                        :
                        <button className="regBtn btnbtn" onClick={this.sendRegData}> {this.state.loading ? <img className="loadingsvg" src={lod} /> : "Register"}</button>

                    }


                </div>







            </div>

        )
    }
}



export default LoginReg;